export default {
	components: {},
	destroyed() {

	},
	data() {
		return {
			big: "",
			bigfocus: true,
			smallbigfocus: false,
			smallbig: "",

			ErrorRemind: "",
			InWareHouseNumber: 0,
			bigdisabled: "disabled",
			//TotalInpectionBig:0,
			//InpectionSmallBigData : []
		}
	},
	onLoad() {
		this.bigfocus = true;
		this.big = '';
		this.smallbigfocus = false;
		this.smallbig = '';
		this.ErrorRemind = '';
		this.InWareHouseNumber = 0;
		this.bigdisabled = "disabled";
		//this.TotalInpectionBig = 0;
		//this.InpectionSmallBigData = [];
	},
	onShow() {
		this.bigfocus = true;
		this.big = '';
		this.smallbigfocus = false;
		this.smallbig = '';
		this.ErrorRemind = '';
		this.InWareHouseNumber = 0;
		this.bigdisabled = "disabled";
		//this.TotalInpectionBig = 0;
		//this.InpectionSmallBigData = [];
	},
	methods: {
		focus_big() {
			if (this.big.length > 0) {
				this.big = '';
				this.smallbig = '';
				//this.TotalInpectionBig = 0;
			}
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		smallbig_focus() {
			if (this.smallbig.length > 0) {
				this.smallbig = '';
			}
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		//大包
		big_confirm() {
			console.log('该大包：' + this.big);
			var big = this.big.trim();
			if (big.length < 1) {
				this.bigdisabled = "disabled";
				this.TotalInpectionBig = 0;
				return;
			}
			var user = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			var Url = this.$websiteUrl + '/api/mbag/getstatus?bagid=' + big + '&user=' + user + '&token=' + token;
			uni.request({
				url: Url,
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));

					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						if (res.data.result == '1') {
							this.TotalInpectionBig = 0;
							this.ErrorRemind = '大包号' + this.big + '未入库，请先做入库操作';
							this.big = '';
						} else if (res.data.result == '5') {
							this.TotalInpectionBig = 0;
							this.ErrorRemind = '大包号' + this.big + '已出库，请查看';
							this.big = '';
						} else {
							this.ErrorRemind = '';
							//this.InWareHouseNumber = res.data.result == null ? 0 : res.data.result;

							this.bigfocus = false;
							this.smallbigfocus = true;
							// this.TotalInpectionBig = res.data.result;
							this.bigdisabled = false;
							// this.TotalBigStatus =true;

						}

					} else {
						this.TotalInpectionBig = 0;
						this.ErrorRemind = res.data.text;
						this.big = '';
					}
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});

		},
		//小包
		smallbig_confirm() {
			var big = this.big.trim();
			if (big.length < 1) {
				this.bigdisabled = "disabled";
				this.TotalInpectionBig = 0;
				this.ErrorRemind = '请先填充大包号';
				return;
			}

			var smallbig = this.smallbig.trim();
			if (smallbig.length < 1) {
				return;
			}
			var user = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			var url = this.$websiteUrl + '/api/mpackage/getreturn?assbillno=' + smallbig + '&bagid=' + big + '&user=' + user +
				'&token=' + token;
			uni.request({
				url: url,
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));
					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						this.ErrorRemind = '';
						this.smallbig = '';
						this.smallbigfocus = true;
						this.InWareHouseNumber = res.data.result == null ? 0 : res.data.result;

						console.log(this.InWareHouseNumber);
					} else {
						this.smallbig = '';
						this.smallbigfocus = true;
						this.ErrorRemind = res.data.text;
					}
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});
		},
	}
}
